@font-face {
  font-family: 'TT Ramillas';
  src: url('../public/fonts/TT Ramillas Initials Trial Black.ttf') format('ttf');
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FCF7E9;
  font-family: TT Ramillas;
  color: #343129
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer img {
  width: 100%;
  height: auto; 
}

.header img{
  width: 100%;
  height: auto; 
}

h2 {
  font-size: 48px;
  text-align: center;
}

p {
  font-size: 20px;
  text-align: justify;
}

label {
  font-size: 20px;
}

.user-form{
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.help-text {
  font-size: 14px;
}

.image-button {
  border: none; /* Убираем границу */
  background: none; /* Убираем заливку */
  padding: 0; /* Убираем внутренние отступы */
}

.first-img {
  width: 100%;
  height: auto;
}

.img-in-button {
  width: 90%;
  height: auto;
}

.event-block {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.event-date {
  width: 80px;
  text-align: center;
  font-weight: bold;
}

.event-details {
  flex-grow: 1;
}

.event-title {
  text-align: center;
}

.event-text {
  text-align: justify;
}

.event-block {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.event-date {
  margin-right: 20px;
  border-right: 2px solid black;
  padding-right: 20px; /* Добавляем немного отступа между датой и вертикальной чертой */
}

.event-content {
  display: flex;
  flex-direction: column;
}

.event-title,
.event-text {
  margin: 0;
}